#section1{
    width: 100%;
    .container{
        width: 100%;
        max-width: 1792px;
        .gap {
            width:100%;
            .title {
                
            }
            .content {
                position: relative;
                height: 941.914px;
                a {
                    img {
                        width: 100%;
                       
                    }
                }
                .left {
                    width: 100%;
                    ul {
                        position: absolute;
                        top: 25px;
                        left: 25px;
                        
                        li {
                            padding: 12px 0;
                            h4 {
                                font-size: 14px;
                                font-weight: 700;
                                color: rgb(0,0,0);
                            }
                        }
                    }
                }
                .center {
                   position: relative;
                    ul {
                        position: absolute;
                        top:0;
                        li {
                            
                            a {
                                text-align: center;
                                img {
                                    &:nth-child(1) {
                                        width: 100%;
                                         height: 100.297px;
                                         text-align: center;
                                    }
                                    &:last-child{
                                         width: 290px;
                                         height: 39px;
 
                                    }
                                 }
                                h2 {
                                    font-size: 17px;
                                    text-align: center;
                                }

                            }
                        }
                    }
                   
                }
                .right {
                    ul { 
                        position: absolute;
                        top: 0;
                        right: 0;

                        li {
                            display: flex;
                            align-items: center;
                            
                            
                            span {

                            }
                            h4 {
                                font-size: 12px;
                                font-weight: 700;
                                line-height: 17px;
                            
                            }
                        }
                    }

                }
            }
        }
    }
}